.location {
  padding: 0 0 60px 0;
}

.location__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 14px;
  background-color: rgba(255, 255, 255, 0.85);
  border: 1px solid rgba(255, 255, 255, 0.95);
  border-radius: 25px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

.location__container {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 44px;
  border-radius: 30px;
  border: solid 5px transparent;
  position: relative;
  border: 5px solid #F7971E;
}

.location__title {
  margin: 0 0 60px 0;
  color: #243976;
  font-size: 50px;
  font-family: ALS Schlange Sans, Arial, Helvetica, sans-serif;
  text-align: center;
}

.location__contacts {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 22px 0 0;
}

.location__contactsList {
  margin: 40px 0 0 0;
  padding: 0;
  color: #243976;
  list-style-type: none;
  text-align: center;
}

.location__contactsListItem {
  margin: 0 0 40px 0;
}

.location__contactsListItem:last-child {
  margin: 0;
}

.location__contactsTitle {
  margin: 0;
  color: #243976;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
}

.location__paragraph {
  margin: 10px 0 0 0;
  font-family: Gotham Pro, Arial, Helvetica, sans-serif;
  font-size: 15px;
  font-weight: 400;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}

.location__icon {
  width: 24px;
  height: 24px;
  margin: 0 8px 0 0;
}

.location__map {
  position: relative;
}

.location__iframe {
  width: 512px;
  height: 464px;
  position: relative;
  border: none;
  border-radius: 30px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.1);
}

@media screen and (max-width: 984px) {
  .location {
    padding: 14px 0;
  }
  .location__title {
    margin: 0 0 14px 0;
  }
  .location__container {
    flex-direction: column;
    padding: 24px;
  }
  .location__contacts {
    margin: 0;
  }
}
@media screen and (max-width: 630px) {
  .location__iframe {
    width: 100%;
  }
  .location__container {
    padding: 0;
    border: none;
  }
  .location__contacts {
    margin: 0 0 20px 0;
  }
  .location__contactsList {
    margin: 20px 0 0 0;
  }
  .location__contactsListItem {
    margin: 0 0 20px 0;
  }
  .location__map {
    width: 100%;
  }
}/*# sourceMappingURL=Location.css.map */